        this.$router.push({ path: '/menu-one/submenu1', query: { step } })
        clearTimeout(timer)
      }, 600)
    },
    openLinkDialog () {
      this.linkDialog = true
      const rows = [this.linkList[1], this.linkList[2]]
      this.$nextTick(() => {
        rows.forEach(row => {
          this.$refs.linkTable.toggleRowSelection(row, true)
        })
      })
    },
